import MaintenanceList from '../components/maintenanceList/MaintenanceList';
import SettingsIcon from '@/components/icons/statistics/SettingIcon';
import UserIcon from '@/components/icons/statistics/UserIcon';
import CalendarIcon from '@/components/icons/statistics/CalendarIcon';
import CarIcon from '@/components/icons/statistics/CarIcon';
import { Card } from '@/types/statistics';
import StatisticsCards from '../../../components/cards/StatisticsCards';

const cards: Card[] = [
    { text: 'زيارة خلال الشهر', icon: <CalendarIcon />, num: 12, percent: 0 },
    { text: 'معطل', icon: <UserIcon />, num: 2, percent: -0.3 },
    { text: 'جاري الصيانة', icon: <SettingsIcon />, num: 15, percent: 0.3, negative: true },
    { text: 'تم تصليح', icon: <CarIcon />, num: 1500, percent: 12 },
];
const MaintenanceListPage = () => {
    return (
        <div className="mt-6 w-full">
            <StatisticsCards cards={cards} />
            <div className="mt-6">
                <MaintenanceList />
            </div>
        </div>
    );
};

export default MaintenanceListPage;
