import { ColumnDef, Row } from '@tanstack/react-table';
import { format } from 'date-fns';
import TrashIcon from '@/components/icons/TrashIcon';
import EditIcon from '@/components/icons/EditIcon';
import { useNavigate } from 'react-router-dom';
import { MaintenanceOrder } from '@/types/maintenance';
import DeletePopup from '@/components/Popups/DeletePopup';
import { endpoints } from '@/lib/axios/axios-global';
import { toast } from 'sonner';
import { useState } from 'react';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<MaintenanceOrder> }) => {
    const navigate = useNavigate();
    const [showDeletePopup, setShowDeletePopup] = useState(false);

    const handleDelete = () => {
        setShowDeletePopup(true);
    };

    const handleCancel = () => {
        setShowDeletePopup(false);
    };

    const handleSuccess = () => {
        toast.error('تم حذف طلب الصيانة بنجاح');
        setShowDeletePopup(false);
        row.original.refreshTable?.();
    };

    return (
        <>
            <div className="flex items-center gap-7">
                <button
                    onClick={() => navigate(`/maintenance/${row.original.id}/edit`)}
                    className="text-primary hover:text-primary/80"
                >
                    <EditIcon className="text-primery-blue-400" />
                </button>
                <button onClick={handleDelete}>
                    <TrashIcon className="text-[#E81E1E] cursor-pointer" />
                </button>
            </div>
            {showDeletePopup && (
                <DeletePopup
                    open={showDeletePopup}
                    text="هل أنت متاكد أنك تريد حذف طلب الصيانة"
                    endpoint={endpoints.maintenance.delete + row.original.id}
                    handleCancel={handleCancel}
                    handleSuccess={handleSuccess}
                />
            )}
        </>
    );
};

export const columns: ColumnDef<MaintenanceOrder>[] = [
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم السيارة والموديل
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="flex flex-row items-center justify-start gap-2">
                <div>
                    <div>{`${row.original.vehicle.brand.name} ${row.original.vehicle.brand_model.name}`}</div>
                    <div>{row.original.vehicle.make_year}</div>
                </div>
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الحالة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="p-1 w-fit bg-[#F59E0B] rounded text-xs text-white-blue-50 font-medium tracking-normal leading-[13.8px]">
                    {/* {row.original.status} */}
                    فى الصيانة
                </div>
            );
        },
    },
    {
        accessorKey: 'date',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    التاريخ
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {format(row.original.created_at, 'yyyy-MM-dd')}
                </div>
            );
        },
    },
    {
        accessorKey: 'service',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الخدمة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.maintenance_type}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    المكان
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.maintenance_center?.name}
                </div>
            );
        },
    },
    {
        id: 'actions',
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
